* {
    color: #393c41;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Rubik", sans-serif;
}

body{
  width: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
}

body::-webkit-scrollbar{
  display: none;
}

a{
  text-decoration: none;
}

@keyframes animateDown{
  0%,
  20%,
  50%,
  80%,
  100%{
    transform: translateY(0);
  }
  40%{
    transform: translateY(-5px);
  }
  60%{
    transform: translateY(-3px);
  }
}


